import { Component } from '@angular/core';
import { IonicPage, AlertController, NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-make-deposit',
  segment: 'make-deposit'
})

@Component({
  selector: 'page-make-deposit',
  templateUrl: 'make-deposit.html'
})

export class MakeDepositPage {
  body: any;
  iPaymentId: any;
  PayNow_link: any;
  iApiTransactionId: any;
  item: any;
  item1: any;
  key: any;
  data: any
  legalid: any;
  totalamount: any;
  amount: any;
  countryid: any;
  complete: any;
  timezero: any;
  purchase: boolean;
  check: boolean;

  constructor(public navCtrl: NavController,public translate: TranslateService, public navparams: NavParams, public user: UserProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
    this.translate.setDefaultLang('en');
		this.translate.use('es');
    this.amount = this.navparams.get('shares');
    this.totalamount = this.navparams.get('Totalamount');
    this.legalid = this.navparams.get('legalid');
    this.countryid = this.navparams.get('countryId');
    this.complete = this.navparams.get('complete');
    this.purchase = this.navparams.get('purchase');
    // alert(this.purchase);

    console.log(this.complete);

    this.user.finance().subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);
        this.check = this.user.checkDataEmpty(data2.data);
					console.log(this.check);
					if (this.check) {
						this.navCtrl.setRoot('page-general_error');

					} else {

            console.log();
            this.data = data2.data;
            console.log(this.data);
            this.timezero = data2.data[0];
            console.log(this.timezero);
					}

       
      },error => {
				console.log(error.JSON);
				setTimeout(() => {
	      this.navCtrl.setRoot('page-general_error');
				  }, 1000);
			},
    )


    // console.log(this.body);
    // this.iPaymentId = this.body.iPaymentId;
    // this.PayNow_link = this.body.PayNow_link;
    // this.iApiTransactionId =  this.body.iApiTransactionId;

    // this.item = this.item1;
    console.log(this.amount, this.totalamount, this.legalid, this.countryid);
    // var result = Object.keys(this.item1).map(function(key) {
    //   return [Number(key), this.item1[key]];
    // });

    // this.key = JSON.stringify(Object.keys(this.item));




  }

  keys(): Array<string> {
    return Object.keys(this.item1);
    // console.log(Object.keys(this.item1));
  }


  // process send button
  sendData() {
    // send booking info
    let loader = this.loadingCtrl.create({
      content: "Please wait..."
    });
    // show message
    let toast = this.toastCtrl.create({
      showCloseButton: true,
      cssClass: 'profiles-bg',
      message: 'Your Data was Edited',
      duration: 3000,
      position: 'top'
    });

    loader.present();

    setTimeout(() => {
      loader.dismiss();
      toast.present();
      // back to home page
      this.navCtrl.setRoot('page-home');
    }, 3000)
  }

  // go to wallet page

  close() {
    this.navCtrl.setRoot('page-my-wallet');
  }


  // save data when tranasction faield 
  save() {
    var storedNames = JSON.parse(localStorage.getItem("chekout"));
    console.log(storedNames);
    if (storedNames != null) {

      storedNames.push({
        shares: this.amount,
        ShareValue: this.totalamount,
        legalId: this.legalid,

      })

      localStorage.setItem("chekout", JSON.stringify(storedNames));

      let toast = this.toastCtrl.create({
        message: 'We were unable to complete this request. Please try again later.',
        duration: 3000,
        position: 'top'
      });

      toast.onDidDismiss(() => {
        console.log('Dismissed toast');
        this.navCtrl.setRoot('page-home');

      });

      toast.present();


    } else {

      var names = [];
      names.push({
        shares: this.amount,
        ShareValue: this.totalamount,
        legalId: this.legalid,
      })

      localStorage.setItem("chekout", JSON.stringify(names));

      let toast = this.toastCtrl.create({
        message: 'We were unable to complete this request. Please try again later.',
        duration: 3000,
        position: 'top'
      });

      toast.onDidDismiss(() => {
        console.log('Dismissed toast');
        this.navCtrl.setRoot('page-home');

      });

      toast.present();

    }
  }


  order() {

    var fa = JSON.parse(localStorage.getItem('2fa'));


    if(fa === 0){

      this.translate.get('activate-tfa').subscribe((translated: string) => {
        // console.log(translated);
        // //=> 'Hello world'
      
        // You can call instant() here
        var title = this.translate.instant('activate-tfa.alert.title');
        var message = this.translate.instant('activate-tfa.alert.message');
        var activate_2FA_btn = this.translate.instant('activate-tfa.alert.activate_2FA_btn');
        var continue_without_2FA_btn = this.translate.instant('activate-tfa.alert.continue_without_2FA_btn');

      const alert = this.alertCtrl.create({
        title: title,
        message: message,
        buttons: [
          {
            text: activate_2FA_btn,
            role: 'yes',
            handler: () => {
             this.navCtrl.push('page-my-security');
            }
          },
          {
            text: continue_without_2FA_btn,
            handler: () => {

              this.confrimTransction();
             
             
            }
          }
        ]
      });
      alert.present();

    })

    }else{

      this.confrimTransction();

     
  }
  }

  confrimTransction(){
    this.translate.get('checkout-page-2').subscribe((translated: string) => {
      // console.log(translated);
      // //=> 'Hello world'
    
      // You can call instant() here
      var loader = this.translate.instant('checkout-page-2.list.loader');
      var complete_investment_error_toast = this.translate.instant('checkout-page-2.list.complete_investment_error_toast');

    let loading = this.loadingCtrl.create({
      content: loader
    });
    loading.present();

    this.user.Invest(this.legalid, this.amount).subscribe(
      data => {

        

        let datas = JSON.parse(JSON.stringify(data));
        console.log(datas);
        let data2 = JSON.parse(datas._body);
        this.check = this.user.checkDataEmpty(data2.data);
        console.log(this.check);
        if (this.check) {
          this.navCtrl.setRoot('page-general_error');

        } else {

          if (data2.success) {
            // console.log(data2.data.Orders);
            this.navCtrl.setRoot('page-make-deposit1');
          } else {
            let toast = this.toastCtrl.create({
              message: complete_investment_error_toast,
              duration: 3000,
              position: 'top'
            });

            toast.onDidDismiss(() => {

              console.log('Dismissed toast');



            });

            toast.present();
          }

          
        }

       

      },
      error => {
        console.log(error.JSON);
        setTimeout(() => {
          loading.dismiss();
          this.navCtrl.setRoot('page-general_error');
        }, 1000);
      },
      () => {


        loading.dismiss();


      }
    )
  })
  }

    
}



